/*============================================================================
	COMMON
==============================================================================*/
.container {
	@include wrapper();

	@media (max-width: 1440px) {
		max-width: 1200px;
		padding: 0 20px;
	}

	@media (max-width: 1200px) {
		max-width: 660px;
		padding: 0 20px;
	}

	@media (max-width: 660px) {
		max-width: 570px;
		padding: 0 20px;
	}
}

.row {
	@include row-flex();
}




.section-test {

	.row {
		@include row-flex();
	}

	.col {
		@include col();
		@include size(12);
	}
}

.body {

	&::before {
		content: '';
		position: fixed;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		background: #000;
		opacity: 0;
		visibility: hidden;
		pointer-events: none;
		z-index: 100;
		transition: all 0.3s ease;
	}


	&.overlay {
		position: relative;

		&::before {
			opacity: 0.6;
			visibility: visible;
			pointer-events: all;
		}

		

		.header .submenu{
			z-index: 101;
		}
	}
}

.sticky-nav {
	position: fixed;
	top: 50%;
	right: 50px;
	z-index: 20;
	transform: translateX(100%) translateY(-50%);


	&__item {
		background: $colorMain;
		margin-bottom: 1px;
	}

	.button-primary {
		padding-right: 20px;
		transition: transform 0.3s ease;

		.icon {
			margin-right: 16px;
		}

		&:hover {
			transform: translateX(calc(-100% + 50px));
			background: inherit;
		} 
	}
}

@media (max-width: 660px) {
	
	.sticky-nav {
		top: auto;
		right: auto;
		bottom: 0;
		transform: none;
		width: 100%;
		background: $colorDark;

		&__list {
			display: flex;	
		}

		&__item {
			margin-bottom: 0;
			background: transparent;
			flex: 1 1 auto;
			display: flex;
			justify-content: center;
			position: relative;

			&:not(:last-child)::after {
				content: '';
				width: 1px;
				height: 70%;
				background: $colorDarkGray;
				position: absolute;
				right: 0;
				top: 50%;
				transform: translateY(-50%);
			}
		}

		.button-primary {
			background: transparent;
			flex-direction: column;
			padding-left: 14px;
			padding-right: 14px;

			.icon {
				margin-right: 0;
			}

			&__text {
				font-size: 12px;
			}
		}
	}

}

@media (max-width: 414px) {

	.sticky-nav {

		.button-primary {
			padding-left: 5px;
			padding-right: 5px;
		}
	}

}

.content {
	.container {
		display: flex;
		flex-wrap: wrap;
	}

	&__aside {
		flex: 0 0 300px;
		max-width: 300px;
		margin-right: 60px;

		@media (max-width: 1200px) {
			flex: 0 0 100%;
			max-width: 100%;
			margin-right: 0;
		}
	}

	&__main {
		flex: 1;

		@media (max-width: 1440px){
			max-width: 800px;
			flex: 0 0 800px;
		}

		@media (max-width: 1200px) {
			flex: 0 0 100%;
			max-width: 100%;
		}
	}
}