/*============================================================================
	#Home Page
==============================================================================*/
.main-slider {
	position: relative;
	max-height: 700px;
	overflow: hidden;
	background: #161616;

	@media (max-width: 1200px) {
		max-height: 100%;
		background: white;
	}

	&__slide {
		height: 700px;
		padding: 83px 0 120px;
		box-sizing: border-box;
		display: flex;
		flex-direction: column;
		justify-content: space-between;

		@media (max-width: 1200px) {
			height: 100%;
			padding: 0;
		}
	}

	&__media {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		@media (max-width: 1200px) {
			position: static;
			height: 510px;
		}

		@media (max-width: 660px) {
			height: 380px;
		}

		@media (max-width: 475px) {
			height: 250px;
		}
	}

	&__content {
		position: relative;
		z-index: 1;
	}

	&__title {
		@include H1(white);
		line-height: 1.185;
		text-align: center;
		max-width: 690px;
		margin: 0 auto;
		text-shadow: 0px 4px 15px rgba(0, 0, 0, 0.35);

		@media (max-width: 1200px) {
			text-shadow: none;
			@include H3();
			padding: 40px 0 45px;
			text-align: left;
		}

		@media (max-width: 475px) {
			padding: 22px 0 27px;
		}
	}

	&__footer {
		position: relative;
		z-index: 1;

		.container {
			display: flex;
			justify-content: center;

			@media (max-width: 660px) {
				flex-direction: column;
				align-items: center;
			}
		}

		.button-primary {
			margin: 0 13px;
			padding-left: 10px;
			padding-right: 10px;
			min-width: 222px;
			height: 50px;

			@media (max-width: 1200px) {
				height: 40px;
			}

			@media (max-width: 660px) {
				max-width: 300px;
				width: 100%;
				margin: 0 0 20px 0;
			}

			@media (max-width: 475px) {
				max-width: 100%;
			}
		}
	}

	&__pagination {
		position: absolute;
		z-index: 1;
		bottom: 23px !important;
		display: flex;
		justify-content: center;

		span {
			width: 50px;
			border-radius: 0;
			height: 4px;
			display: inline-block;
			background: #D7D7D7;
			opacity: 1;

			&.swiper-pagination-bullet-active {
				background: $colorMain;
			}

			&:hover {
				background: $colorMainHover;
			}
		}

		@media (max-width: 1200px) {
			position: static;
			margin-top: 47px;
		}

		@media (max-width: 475px) {
			margin-top: 37px;
			margin-bottom: 13px;
		}
	} 


}

.front-catalog {

	&__header {
		padding: 60px 0 40px;

		@media (max-width: 1200px) {
			padding: 29px 0 29px;
		}
	}

	&__title {
		@include H1();

		@media (max-width: 1200px) {
			@include H3();
		}
	}

	&__model-sort {
		padding-bottom: 50px;

		@media (max-width: 1200px){
			padding-bottom: 30px;
		}

		@media (max-width: 475px){
			padding-bottom: 20px;
		}
	}
}

.model-sort {

	&__content {
		position: relative;

		@media (max-width: 1200px) {
			max-width: 330px;
		}

		@media (max-width: 475px) {
			max-width: 100%;
		}
	}

	&__list {
		margin: 0;
		padding: 0;
		list-style-type: none;
		border: 1px solid #D7D7D7;
		border-radius: 4px;
		display: inline-flex;
		background: white;

		@media (max-width: 1200px){
			display: block;
			position: absolute;
			width: 100%;
			top: 0;
			transform: translateY(calc(-100% - 20px ));
			z-index: 1;
			opacity: 0;
			visibility: 0;
			pointer-events: none;
			transition: all 0.3s ease;
			
			&._is-visible {
				opacity: 1;
				visibility: 1;
				pointer-events: all;
			}
		}
	}

	&__item {
		position: relative;

		&:not(:last-child)::after {
			content: '';
			position: absolute;
			width: 1px;
			height: 65%;
			background: #D7D7D7;
			top: 50%;
			right: 0;
			transform: translateY(-50%);
			z-index: -1;

			@media (max-width: 1200px){
				width: calc(100% - 40px);
				left: 20px;
				height: 1px;
				top: auto;
				bottom: 0;
			}
		}
	
		&:first-child {

			.button {
				border-top-left-radius: 4px;
				border-bottom-left-radius: 4px;
				padding-left: 30px;

				@media (max-width: 1200px){
					border-top-right-radius: 4px;
					border-bottom-left-radius: 0px;
				}
			}
		}

		&:last-child {

			.button {
				border-top-right-radius: 4px;
				border-bottom-right-radius: 4px;
				padding-right: 30px;

				@media (max-width: 1200px){
					border-top-right-radius: 0px;
					border-bottom-left-radius: 4px;
				}
			}
		}
	}

	&__button {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 13px 24px;
		height: 100%;
		transition: all 0.3s ease;
		

		&._is-active {
			position: relative;
			z-index: 1;
			background: $colorMain;
			color: white;
			cursor: inherit;
			box-shadow: 0 0 1px 1px $colorMain;

			path {
				fill: white !important;
			}
		}

		&:not(._is-active):hover {
			box-shadow: 0 0 1px 1px $colorGray;
			background: $colorGray;
		}

		@media (max-width: 1200px){
			width: 100%;
			justify-content: flex-start;
			padding: 13px 30px;
			height: 54px;
		}
	}

	&__dropdown {
		display: none;
		visibility: hidden;
		max-width: 100%;
		width: 100%;
		justify-content: space-between;
		padding-left: 15px;

		&._is-opened {
			color: $colorMain;
			fill: $colorMain;

			.icon {
				transform: rotate(-90deg);
			} 
		}

		@media (max-width: 1200px){
			display: flex;
			visibility: visible;
		}
	}
}

.category-sort {

	@media (max-width: 1200px){
		border-bottom: 1px solid $colorGray;
		overflow-y: auto;
		margin-bottom: 40px;
	}

	@media (max-width: 475px) {
		width: calc(100% + 40px);
		margin-left: -20px;
		padding: 0 20px;
		margin-bottom: 55px;
	}	

	&__list {
		margin: 0;
		padding: 0;
		list-style-type: none;
		margin-bottom: 10px;

		@media (max-width: 1200px){
			display: flex;
			white-space: nowrap;
			margin-bottom: 0;
		}


	}

	&__item {
		transition: opacity 0.3s ease;

		&:not(:last-child) {
			margin-bottom: 6px;

			@media (max-width: 1200px){
				margin-bottom: 0px;
			}	
		}

		.button {
			display: flex;
			align-items: center;
			text-align: left;
			font-size: 20px;
			padding: 10px 0;
			transition: opacity 0.3s ease,
						color 0.3s ease;
			border-bottom: 4px solid transparent;

			@media (max-width: 1200px){
				
				margin-right: 40px;
			}	
			
			&:not(._is-active):hover{
				opacity: 0.6;
			}

			svg {
				transition: fill 0.3s ease;
			}

			.icon-car {
				display: flex;
				justify-content: center;
				align-items: center;
				margin-right: 15px;

				@media (max-width: 1200px){
					display: none;
				}				
			}

			&._is-active {
				color: $colorMain;

				svg { fill: $colorMain }

				@media (max-width: 1200px){
					border-bottom: 4px solid $colorMain;
				}
			}
		}
	}

	> .button {
		width: 100%;
		padding: 5px 10px;

		@media (max-width: 1200px){
			display: none;
		}
	}
}