/*============================================================================
	#Footer
==============================================================================*/
.footer {
	
	&__nav {
		background: $colorDark;
	}

	&__main {
		background: #2D2D2D;
	}

	@media (max-width: 660px) {
		padding-bottom: 58px;

		&__lang {
			margin-bottom: 20px;
		}
	}	

}


.footer-nav {
	padding: 50px 0 50px;

	@media (max-width: 1200px) {
		padding: 40px 0;
	}		

	.container {
		display: flex;
		justify-content: space-between;

		@media (max-width: 1200px) {
			display: block;
		}
	}


	&__item {
		display: block;
		color: white;
		fill: white;
		text-decoration: none;
		@include H3(white);
		background: $colorDarkGray;
		padding: 30px 20px 20px 30px;
		min-height: 250px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		width: calc((100% - 100px ) / 5);
		transition: background 0.3s ease;

		@media (max-width: 1200px) {
			width: 100%;
			margin-bottom: 10px;
			padding: 14px 10px 13px;
			flex-direction: row;
			min-height: auto;
			align-items: center;
			justify-content: flex-start;
			
		}

		&:hover {
			background: $colorMain;
		}
	}

	&__icon {
		height: 100px;
		width: 100px;
		border-radius: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		background: $colorDark;

		@media (max-width: 1200px) {
			margin-right: 20px;
			width: 40px;
			height: 40px;
			flex: 0 0 40px;
		}
	}

	&__text {
		display: flex;
		justify-content: space-between;
		align-items: flex-end;

		@media (max-width: 1200px) {
			@include Header18(white);
			width: 100%;
		}		
	}
}

.footer-main {

	&__top {
		padding: 30px 0;

		@media (max-width: 1200px) {
			padding: 26px 0 12px;

			.button-totop {
				margin: 0 auto 30px;
			}
		}

		.container {
			display: flex;
			align-items: center;
			justify-content: space-between;

			@media (max-width: 1200px) {
				flex-direction: column-reverse;
				align-items: flex-start;
			}	
		}
	}

	&__breadcrumbs {
		color: $colorGray;
		fill: $colorGray;
		overflow-y: scroll;
    	width: 100%;

		.breadcrumbs__list {
			white-space: nowrap;
			flex-wrap: nowrap;
			padding-bottom: 8px;
		}

		@media (max-width: 660px){

		}
	}

	&__body {
	

		.row {
		    margin-right: 0px;
    		margin-left: 0px;
			padding-top: 30px;
			padding-bottom: 30px;
			border-top: 1px solid #616161;
			border-bottom: 1px solid #616161;
			justify-content: space-between;

			@media (max-width: 1200px) {
				padding-top: 20px;
			}	
		}
	}

	&__subscribe {
		flex: 0 0 630px;
		max-width: 630px;

		@media (max-width: 1200px) {
			flex: 0 0 100%;
			max-width: 100%;
			margin-bottom: 40px;
		}			
	}

	&__bottom {
		padding: 23px 0;
		color: $colorGray;
		font-size: 14px;
		line-height: 1.52;

		@media (max-width: 660px) {
			padding-bottom: 8px;
		}

		.container {
			display: flex;
			justify-content: space-between;

			@media (max-width: 660px) {
				flex-direction: column;
				align-items: center;
			}
		}
	}

	&__social {
		margin: 0 auto;
	} 
}

.subscribe {

	&__title {
		color: white;
		margin-bottom: 13px;

		@media (max-width: 1200px) {
			max-width: 330px;
		}	
	}

	&__descr {
		@include Header18(white);
		margin: 0;

		@media (max-width: 1200px) {
			max-width: 330px;
			font-size: 16px;
			line-height: 1.52;
		}	
	}

	&__form {
		margin-top: 23px;
		display: flex;
		align-items: flex-end;

		@media (max-width: 1200px) {
			margin-top: 16px;
		}

		@media (max-width: 660px) {
			flex-wrap: wrap;
		}

		.button-primary {
			margin-left: 20px;
			width: 144px;
			height: 40px;
			flex: 0 0 auto;

			@media (max-width: 660px) {
				margin-left: 0px;
				margin-right: 20px;
			}

			@media (max-width: 375px) {
				max-width: calc(50% - 10px);
				flex: 1 1 calc(50% - 10px);
				// width: calc();
				margin-right: 20px;

				&:last-child {
				margin-right: 0;}
			}
		}

		.label {
			color: white;
		}

	}

	.form-group {
		margin-bottom: 25px;
	}
}

.social {
	color: white;
	fill: white;

	&__title {
		color: white;
		margin-bottom: 24px;

		@media (max-width: 1200px) {
			display: none;
		}
	}

	&__list {
		display: flex;
	}

	&__item {
		width: 50px;
		height: 50px;
		background: $colorDarkGray;
		display: flex;
		justify-content: center;
		align-items: center;
		transition: background 0.3s ease;
		fill: white;
		
		&:not(:last-child){
			margin-right: 10px;
		}

		&:hover {
			background: $colorMain;

			fill: $colorDarkGray;
		}
	}
}

.lang {

	.button {
		font-size: 14px;
		color: $colorGray;
		position: relative;

		&:not(:last-child){
			margin-right: 20px;
			padding-right: 20px;


			&::after {
				content: '';
				position: absolute;
				height: 20px;
				width: 1px;
				background: #3C3C3C;
				right: 0;
				top: calc(50% - 10px);

			}

		}

		&:hover {
			text-decoration: underline;
		}
	}
}

.footer-menu {


	&__list {
		margin: 0;
		padding: 0;
		list-style-type: none;
		display: flex;

		@media (max-width: 660px) {
			flex-wrap: wrap;
			justify-content: center;
		}
	}

	&__item {
		position: relative;

		&:not(:last-child){
			margin-right: 20px;
			padding-right: 20px;


			&::after {
				content: '';
				position: absolute;
				height: 20px;
				width: 1px;
				background: #3C3C3C;
				right: 0;
				top: calc(50% - 10px);

				@media (max-width: 660px) {
					display: none;
				}
			}
			

		}

		@media (max-width: 660px) {
			margin-right: 0 !important;
			padding: 0 15px !important;
			margin-bottom: 15px;
		}
	}

	&__link {
		color: $colorGray;
		text-decoration: none;

		&:hover {
			text-decoration: underline;
		}
	}
}